<template>
  <a-modal 
    :width="700"
    :visible="isShow"
    :closable="true"
    :title="$t('info.version_info')"
    :destroyOnClose="true"  
    :footer="null"
    @cancel="cancel"
    :body-style="{ padding: 0 }"
  >
    <div slot="extra">
      <span><a-icon type="close" class="close" @click="cancel"/></span>
    </div>
    <div class="info-class">
      <img src="@/assets/version.png" alt="version" class="info-img">
      <span class="title">{{ $store.state.app.menuTitle }}</span>
      <span>{{ $t('info.app_version') }}: &nbsp;{{ data.appVersion }}</span><br>
      <span>{{ $t('info.app_build_time') }}: &nbsp;<pick-label :defaultValue="data.appBuildTime" /></span><br>
      <span>{{ $t('info.app_pom_time') }}: &nbsp;<pick-label :defaultValue="data.appPomTime" /></span><br>
      <span>{{ $t('info.db_version') }}: &nbsp;{{ data.dbVersion }}</span><br>
      <span>{{ $t('info.db_upgrate_time') }}: &nbsp;<pick-label :defaultValue="data.dbUpgrateTime" /></span><br>
      <div class="footer">
        <div class="left-logo">
          <img src="@/assets/footer_logo.png" alt="logo" class="logo">&nbsp;&nbsp;
          <span class="footer-info footer-info1">{{ $t('info.company') }}</span>
        </div>
        <span style="line-height: 70px">|</span>
        <div class="right">
          <a class="footer-info" target="_blank" style="text-align: left" href="http://www.inhemeter.com">www.inhemeter.com</a><br>
          <span class="footer-info footer-info1">{{ $t('info.copy_right') }}</span>
        </div>
      </div>
    </div>
  </a-modal>
</template>

<script>
  import {
    axios
  } from '@/utils/request'
  export default {
    data() {
      return {
        isShow: false,
        data: {}
      }
    },
    methods: {
      showModal() {
         this.init()
      },
      init() {
        const that =this
        axios({
          method: 'get',
          url:'auth/version',
        }).then(res => {
          that.data = res.result
          that.isShow = true
        })
      },
      cancel() {
        this.isShow = false
      },
    },
  }
</script>
<style lang="scss" scoped>
.icon {
  width: 30px;
  height: 30px;
}
.close {
  color: #1890ff;
  cursor: pointer;
  font-size: 20px;
}
.info-class {
  text-align: center;
  width: 100%;
  span {
    display: inline-block;
    margin-bottom: 3px;
  }
  .title {
    font-size: 25px;
    color: #12638F;
    font-weight: 500;
    display: block;
    margin-bottom: 15px;
  }
  .info-img {
    display: block;
    margin: 30px auto 20px;
    width: 100px;
    height: 100px;
  }
  .footer {
    margin-top: 50px;
    height: 70px;
    background: #f3f3f3;
    width: 100%;
    text-align: left;
    display: flex;
    .footer-info {
      font-size: 17px;
    }
    .footer-info1 {
      color: #555555;
    }
    .logo {
      width: 22px;
      height: 22px;
    }
    .left-logo {
      padding-left: 60px;
      width: 40%;
      display: inline-block;
      span {
        line-height: 70px;
      }
    }
    .right {
      padding-left: 20px;
      margin-top: 10px;
      width: 59%;
      display: inline-block;
    }
  }
}
</style>