import Vue from 'vue'
import { getWebClientParams } from '@/api/manage'
import {
  SIDEBAR_TYPE,
  DEFAULT_THEME,
  DEFAULT_LAYOUT_MODE,
  DEFAULT_COLOR,
  DEFAULT_COLOR_WEAK,
  DEFAULT_FIXED_HEADER,
  DEFAULT_FIXED_SIDEMENU,
  DEFAULT_FIXED_HEADER_HIDDEN,
  DEFAULT_CONTENT_WIDTH_TYPE,
  DEFAULT_LANG_ID,
  DEFAULT_HEAD_TITLE,
  DEFAULT_HEAD_ICON,
  DEFAULT_MENU_TITLE,
  DEFAULT_FOOT_TITLE
} from '@/store/mutation-types'

const app = {
  state: {
    sidebar: {
      opened: true,
      withoutAnimation: false
    },
    device: 'desktop',
    theme: '',
    layout: '',
    contentWidth: '',
    fixedHeader: false,
    fixSiderbar: false,
    autoHideHeader: false,
    color: null,
    weak: false,
    langId: '',
    headTitle: '',
    headIcon: '',
    menuTitle: '',
    footTitle: ''
  },
  mutations: {
    SET_SIDEBAR_TYPE: (state, type) => {
      state.sidebar.opened = type
      Vue.ls.set(SIDEBAR_TYPE, type)
    },
    CLOSE_SIDEBAR: (state, withoutAnimation) => {
      Vue.ls.set(SIDEBAR_TYPE, true)
      state.sidebar.opened = false
      state.sidebar.withoutAnimation = withoutAnimation
    },
    TOGGLE_DEVICE: (state, device) => {
      state.device = device
    },
    TOGGLE_THEME: (state, theme) => {
      // setStore('_DEFAULT_THEME', theme)
      Vue.ls.set(DEFAULT_THEME, theme)
      state.theme = theme
    },
    TOGGLE_LAYOUT_MODE: (state, layout) => {
      Vue.ls.set(DEFAULT_LAYOUT_MODE, layout)
      state.layout = layout
    },
    TOGGLE_FIXED_HEADER: (state, fixed) => {
      Vue.ls.set(DEFAULT_FIXED_HEADER, fixed)
      state.fixedHeader = fixed
    },
    TOGGLE_FIXED_SIDERBAR: (state, fixed) => {
      Vue.ls.set(DEFAULT_FIXED_SIDEMENU, fixed)
      state.fixSiderbar = fixed
    },
    TOGGLE_FIXED_HEADER_HIDDEN: (state, show) => {
      Vue.ls.set(DEFAULT_FIXED_HEADER_HIDDEN, show)
      state.autoHideHeader = show
    },
    TOGGLE_CONTENT_WIDTH: (state, type) => {
      Vue.ls.set(DEFAULT_CONTENT_WIDTH_TYPE, type)
      state.contentWidth = type
    },
    TOGGLE_COLOR: (state, color) => {
      Vue.ls.set(DEFAULT_COLOR, color)
      state.color = color
    },
    TOGGLE_WEAK: (state, flag) => {
      Vue.ls.set(DEFAULT_COLOR_WEAK, flag)
      state.weak = flag
    },
    SET_LANG_ID: (state, id)=> {
      Vue.ls.set(DEFAULT_LANG_ID, id)
      state.langId = id
    },
    SET_HEAD_TITLE: (state,headTitle) =>{
      Vue.ls.set(DEFAULT_HEAD_TITLE, headTitle)
      state.headTitle = headTitle
    },
    SET_HEAD_ICON: (state,headIcon) =>{
      Vue.ls.set(DEFAULT_HEAD_ICON, headIcon)
      state.headIcon = headIcon
    },
    SET_MENU_TITLE: (state,menuTitle) =>{
      Vue.ls.set(DEFAULT_MENU_TITLE, menuTitle)
      state.menuTitle = menuTitle
    },
    SET_FOOT_TITLE: (state,footTitle) =>{
      Vue.ls.set(DEFAULT_FOOT_TITLE, footTitle)
      state.footTitle = footTitle
    },
  },
  actions: {
    setSidebar: ({ commit }, type) => {
      commit('SET_SIDEBAR_TYPE', type)
    },
    CloseSidebar({ commit }, { withoutAnimation }) {
      commit('CLOSE_SIDEBAR', withoutAnimation)
    },
    ToggleDevice({ commit }, device) {
      commit('TOGGLE_DEVICE', device)
    },
    ToggleTheme({ commit }, theme) {
      commit('TOGGLE_THEME', theme)
    },
    ToggleLayoutMode({ commit }, mode) {
      commit('TOGGLE_LAYOUT_MODE', mode)
    },
    ToggleFixedHeader({ commit }, fixedHeader) {
      if (!fixedHeader) {
        commit('TOGGLE_FIXED_HEADER_HIDDEN', false)
      }
      commit('TOGGLE_FIXED_HEADER', fixedHeader)
    },
    ToggleFixSiderbar({ commit }, fixSiderbar) {
      commit( 'TOGGLE_FIXED_SIDERBAR', fixSiderbar)
    },
    ToggleFixedHeaderHidden({ commit }, show) {
      commit('TOGGLE_FIXED_HEADER_HIDDEN', show)
    },
    ToggleContentWidth({ commit }, type) {
      commit('TOGGLE_CONTENT_WIDTH', type)
    },
    ToggleColor({ commit }, color) {
      commit('TOGGLE_COLOR', color)
    },
    ToggleWeak({ commit }, weakFlag) {
      commit('TOGGLE_WEAK', weakFlag)
    },
    setLangId({ commit }, id){
      commit('SET_LANG_ID', id)
    },
    LoadWebParams({ commit }){
      return new Promise((resolve, reject) => {
        getWebClientParams(localStorage.orgId?localStorage.orgId:'').then(response => {
          const result = response.result
          result.HEAD_ICON = (process.env.NODE_ENV === 'production'? '/inhe-platform-mdm/': '/') + result.HEAD_ICON
          commit('SET_HEAD_TITLE', result.HEAD_TITLE)
          document.title = result.HEAD_TITLE
          commit('SET_HEAD_ICON', result.HEAD_ICON)
          const $favicon = document.createElement('link')
          $favicon.rel = 'icon'
          $favicon.href = result.HEAD_ICON
          document.head.appendChild($favicon)
          commit('SET_MENU_TITLE', result.MENU_TITLE)
          commit('SET_FOOT_TITLE', result.FOOT_TITLE)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    }
  }
}

export default app