import Vue from 'vue'
import axios from 'axios'
import store from '@/store'
import { VueAxios } from './axios'
import notification from 'ant-design-vue/es/notification'
import { ACCESS_TOKEN } from '@/store/mutation-types'
import { isNumber } from 'util'
import { $lang } from '@/i18n/i18n'

// 创建 axios 实例
const service = axios.create({
  baseURL: process.env.NODE_ENV === 'production'?'/inhe-platform-mdm/api':'/api', // api base_url
  timeout: 6000 // 请求超时时间
})

const err = (error) => {
  if (error.response) {
    const data = error.response.data
    const token = Vue.ls.get(ACCESS_TOKEN)
    if (error.response.status === 403) {
      notification.error({ message: 'Forbidden', description: data.message})
    }
    else
    if (error.response.status === 401) {
      notification.error({ message: 'Unauthorized', description: 'Authorization verification failed' })
      if (token) {
        store.dispatch('Logout').then(() => {})
      }
    }
    else{
      notification.error({ message: 'Unknow error', description: error+'' })
    }
  }
  return Promise.reject(error)
}

// request interceptor
service.interceptors.request.use(config => {
  const token = Vue.ls.get(ACCESS_TOKEN)
  if (token) {
    config.headers[ 'Access-Token' ] = token // 让每个请求携带自定义 token 请根据实际情况自行修改
  }
  return config
}, err)

// response interceptor
service.interceptors.response.use((response) => {
    const result = response.data
    
    if(!(result instanceof Blob) && (!result || result.code === undefined)){
      notification.error({ 
        message: 'Error in return value', 
        description: 'result: ' + result
      })
      return null
    }
    const token = response.headers.refresh_token
    const exp = response.headers.refresh_exp * 1
    if(token && isNumber(exp)){
      Vue.ls.set(ACCESS_TOKEN, token, exp * 1000)
    }
    if(!(result instanceof Blob) && result.code < 0){
      let description = $lang('err', result.code)
      if(!description){
        description = result.message?result.message:'unknow error'
      }
      notification.error({ 
        message: result.code, 
        description: description
      })
      if(result.code == -100017 || result.code == -100018){
        store.dispatch('Logout').then(() => {})
      }
      return null
    }
    return result
  }, err)

const installer = {
  vm: {},
  install (Vue, router = {}) {
    Vue.use(VueAxios, router, service)
  }
}

export {
  installer as VueAxios,
  service as axios
}