<template>
  <span>
    <span v-if="symbol">{{ moneyUnit }}</span>
    <span v-else>
      <span v-if="!input">
        <span>{{ formatter(value) }}</span>&nbsp;
        <span v-if="unit">{{ moneyUnit }}</span>
      </span>
      <span v-else>
        <a-input-number
          :defaultValue="value"
          :precision="precision"
          :formatter="formatter"
          :parser="parser"
          @change="onChange"
          :prefix="moneyUnit"
        />&nbsp;
        <span>{{ moneyUnit }}</span>
      </span>  
    </span>
  </span>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'Money',
  data(){
    return {
      precision: 0
    }
  },
  props: {
    value: {
      type: Number,
      default: 0
    },
    unit: {
      type: Boolean,
      default: true
    },
    input: {
      type: Boolean,
      default: false
    },
    symbol: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapState({
      moneyUnit: state => state.user.moneyUnit,
    })
  },
  methods: {
    onChange(value) {
      this.$emit('change',value)
    },
    formatter(value) {
      return (value+'').replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    },
    parser(value){
      return value.replace(/\$\s?|(,*)/g, '')
    }
  }
}
</script>