<template>
  <span>
    <a-switch 
      :checkedChildren="!checkedChildren?$t('button._006_on'):checkedChildren" 
      :unCheckedChildren="!unCheckedChildren?$t('button._006_off'):unCheckedChildren" 
      :defaultChecked="defaultChecked"
      :disabled="disabled || defDisabled"
      v-model="checked"
      @change="onChange"
    />
  </span>
</template>
<script>
export default {
  name: 'BtnSwitch',
  data() {
    return {
      id: '006',
      defDisabled: true,
      checked: this.defaultChecked
    }
  },
  props: {
    defaultChecked: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    confirm: {
      type: Boolean,
      default: true
    },
    checkedChildren: {
      type: String,
      default: null
    },
    unCheckedChildren: {
      type: String,
      default: null
    }
  },
  watch:{
    defaultChecked(value){
      this.checked = value
    }
  },
  created() {
    const that = this
    that.$store._vm.roles.permissions.forEach(element => {
      if(element['permissionId']==that.$route.name && element['actionList'].indexOf(that.id)>=0){
        that.defDisabled = false
      }
    })
  },
  methods: {
    onChange(checked){
      const that = this
      if(that.confirm){
        that.$confirm({
          title: that.$t('button._006_msg1'),
          content: checked?that.$t('button._006_msg2'):that.$t('button._006_msg3'),
          okType: 'danger',
          onOk() {
            that.$emit('change',checked)
          },
          onCancel(){
            that.checked = !checked
          }
        })
      }
      else{
        that.$emit('change',checked)
      }
      
    }
  }
}
</script>