<template>
  <span v-if="visible">
    <a-input-search
      :placeholder="this.$t('button._001_placeholder')"
      v-model="seaValue"
      @search="onSearch"
      @change="onChange"
      enterButton
      v-if="showInput"
    />
    <a-button @click="onSearch" v-if="!showInput" :disabled="disabled" class="searchBtn">{{ text? text : $t('button._001') }}</a-button>
  </span>
</template>
<script>
export default {
  name: 'BtnSearch',
  data() {
    return {
      id: '001',
      visible: false,
      seaValue: ''
    }
  },
  props: {
    showInput:{
      type: Boolean,
      default: true
    },
    value: {
      type: String,
      default: ''
    },
    text: {
      type: String,
      default: ''
    },
    disabled:{
      type: Boolean,
      default: false
    }
  },
  watch: {
    value(v) {
      this.seaValue = v
    }
  },
  created() {
    const that = this
    that.$store._vm.roles.permissions.forEach(element => {
      if(element['permissionId']==that.$route.name && element['actionList'].indexOf(that.id)>=0){
        that.visible = true
      }
    })
  },
  methods: {
    onSearch(value){
      if(this.showInput){
        this.$emit('click',value)
      }
      else{
        this.$emit('click')
      }
    },
    onChange(){
      this.$emit('change',this.seaValue)
    }
  }
}
</script>
<style lang="scss" scoped>
  .searchBtn{
    background-color: #52c41a;
    border-color: #52c41a;
    color: #ffffff;
  }
  .searchBtn:hover{
    background-color: #52c41aa3;
    color: #ffffff;
    border-color: #52c41a;
  }
</style>