<template>
  <span v-if="visible">
    <a-button type="primary" icon="solution" @click="click" v-if="type=='bt'">{{ !text?$t('button._005'):text }}</a-button>
    <a @click="click" v-if="type=='a'">{{ !text?$t('button._005'):text }}</a>
    <span @click="click" v-if="type=='b'">{{ !text?$t('button._005'):text }}</span>
  </span>
</template>
<script>
export default {
  name: 'BtnDetail',
  data() {
    return {
      id: '005',
      visible: false
    }
  },
  props: {
    type: {
      type: String,
      default: 'bt'
    },
    text: {
      type: String,
      default: null
    }
  },
  created() {
    const that = this
    that.$store._vm.roles.permissions.forEach(element => {
      if(element['permissionId']==that.$route.name && element['actionList'].indexOf(that.id)>=0){
        that.visible = true
      }
    })
  },
  methods: {
    click(){
      this.$emit('click')
    }
  }
}
</script>