<template>
  <span>
    <a-range-picker
      style="width: 100%;"
      :showTime="showLevel=='0'?true:false"
      :mode="mode"
      :format="format"
      :value="value"
      :ranges="{ Today: [moment(), moment()], 'This Month': [moment(), moment().endOf('month')] }"
      @change="onChange"
      @panelChange="onPanelChange"
    />
  </span>
</template>
<script>
import moment from 'moment'
import { toFormatDate, toUTC, getOffset } from '@/inhe/picker/utils'
export default {
  name: 'PickRange',
  data(){
    return {
      format: 'YYYY-MM-DD',
      mode: ['date','date'],
      offset: 0,
      value: null
    }
  },
  props: {
    defaultValue: {
      type: Array,
      default: null
    },
    showLevel: {
      type: String,
      default: '0'
    }
  },
  watch: {
    defaultValue(value) {
      if(value){
        this.value = [value[0]?this.formatDate(value[0]):null,value[1]?this.formatDate(value[1]):null]
      }
    }
  },
  created(){
    if(this.showLevel==='0'){
      this.format = this.$store._vm.userInfo.dateFormat.replace(/y/g, 'Y').replace(/d/g, 'D')
    }
    else
    if(this.showLevel==='1'){
      this.format = this.$store._vm.userInfo.dateFormat.replace(/y/g, 'Y').replace(/d/g, 'D').replace(/[H,m,s,:,\s]/g,'')
    }
    else
    if(this.showLevel==='2'){
      this.mode = ['month','month']
      let fmt =     this.$store._vm.userInfo.dateFormat.replace(/y/g, 'Y').replace(/[d,H,m,s,:,\s]/g,'')
      const start = fmt.substr(0,1)
      if(start != 'Y' && start != 'M') fmt = fmt.substr(1)
      const end = fmt.substr(fmt.length-1,1)
      if(end != 'Y' && end != 'M') fmt = fmt.substr(0,fmt.length-1)
      this.format = fmt
    }
    this.offset = getOffset(this.$store._vm.userInfo.timezone)
    if(this.defaultValue){
      this.value = [this.defaultValue[0]?this.formatDate(this.defaultValue[0]):null,this.defaultValue[1]?this.formatDate(this.defaultValue[1]):null]
    }
  },
  methods: {
    moment,
    formatDate(value){
      if(!value){
        return null
      }
      const date = new Date(value)
      date.setMinutes(date.getMinutes() + this.offset)
      return moment(toFormatDate(date, this.format.replace(/Y/g, 'y').replace(/D/g, 'd')), this.format)
    },
    onPanelChange(value){
      if(this.showLevel==='2'){
        this.onChange(value)
      }
    },
    onChange(value) {
      this.value = value
      if(value&&value.length>1){
        const date0 = new Date(value[0])
        date0.setMinutes(date0.getMinutes() - this.offset)
        const date1 = new Date(value[1])
        date1.setMinutes(date1.getMinutes() - this.offset)
        this.$emit('change',[toUTC(date0),toUTC(date1)])
      }
      else{
        this.$emit('change')
      }
    }
  }
}
</script>