<template>
  <span v-if="visible">
    <a-button type="primary" icon="printer" @click="click" v-if="type=='bt'">{{ $t('button._009') }}</a-button>
    <a-button type="primary" @click="click" v-if="type=='btNoIcon'">{{ $t('button._009') }}</a-button>
    <a @click="click" v-if="type=='a'">{{ $t('button._009') }}</a>
    <span @click="click" v-if="type=='b'">{{ $t('button._009') }}</span>
  </span>
</template>
<script>
export default {
  name: 'BtnPrint',
  data() {
    return {
      id: '009',
      visible: false
    }
  },
  props: {
    type: {
      type: String,
      default: 'bt'
    },
    printDiv:{
      type:String,
      default:''
    },
    confirm: {
      type: Boolean,
      default: true
    }
  },
  created() {
    const that = this
    console.log(that.printDiv)
    that.$store._vm.roles.permissions.forEach(element => {
      if(element['permissionId']==that.$route.name && element['actionList'].indexOf(that.id)>=0){
        that.visible = true
      }
    })
  },
  methods: {
    click(){
      const that = this
      if(that.confirm){
        that.$confirm({
          title: that.$t('button._009_msg1'),
          content: that.$t('button._009_msg2'),
          okType: 'danger',
          onOk() {
            that.$emit('click')
          }
        })
      }
      else{
        that.$emit('click')
      }
    }
  }
}
</script>