<template>
  <span>
    <a-month-picker 
      :format="format"
      :value="value"
      :disabled="disabled"
      @change="onChange" 
    />
  </span>
</template>
<script>
import moment from 'moment'
import { toFormatDate, toUTC, getOffset } from '@/inhe/picker/utils'
export default {
  name: 'PickMonth',
  data(){
    return {
      format: 'YYYY-MM',
      offset: 0,
      value: ''
    }
  },
  props: {
    defaultValue: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
  },
  watch: {
    defaultValue(value) {
      this.value = this.formatDate(value)
    }
  },
  created(){
    let fmt = this.$store._vm.userInfo.dateFormat.replace(/[d,H,m,s,:,\s]/g,'').replace(/y/g, 'Y')
    const start = fmt.substr(0,1)
    if(start != 'Y' && start != 'M') fmt = fmt.substr(1)
    const end = fmt.substr(fmt.length-1,1)
    if(end != 'Y' && end != 'M') fmt = fmt.substr(0,fmt.length-1)
    this.format = fmt
    this.offset = getOffset(this.$store._vm.userInfo.timezone)
    this.value = this.formatDate(this.defaultValue)
  },
  methods: {
    formatDate(value){
      if(!value){
        return null
      }
      const date = new Date(value)
      date.setMinutes(date.getMinutes() + this.offset)
      return moment(toFormatDate(date, this.format.replace(/Y/g, 'y')), this.format)
    },
    onChange(value) {
      this.value = value
      if(value){
        const date = new Date(value)
        date.setMinutes(date.getMinutes() - this.offset)
        this.$emit('change', toUTC(date))
      }
      else{
        this.$emit('change')
      }
    }
  }
}
</script>