import Vue from 'vue'
import { login, getInfo, logout } from '@/api/login'
import { ACCESS_TOKEN } from '@/store/mutation-types'
import { welcome } from '@/utils/util'

const user = {
  state: {
    userId: '',
    token: '',
    name: '',
    welcome: '',
    avatar: '',
    moneyUnit: '',
    timezone: '',
    deptId: '',
    dateFormat: '',
    mobile: '',
    email:'',
    roles: [],
    info: {}
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_NAME: (state, { name, welcome }) => {
      state.name = name
      state.welcome = welcome
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar
    },
    SET_MONEY_UNIT: (state, moneyUnit) => {
      state.moneyUnit = moneyUnit
    },
    SET_TIMEZONE: (state, timezone) => {
      state.timezone = timezone
    },
    SET_DEPT_ID: (state, deptId) => {
      state.deptId = deptId
    },
    SET_DATE_FORMAT: (state, dateFormat) => {
      state.dateFormat = dateFormat
    },
    SET_MOBILE: (state, mobile) => {
      state.mobile = mobile
    },
    SET_EMAIL: (state, email) => {
      state.email = email
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
    },
    SET_INFO: (state, info) => {
      state.info = info
    },
    SET_USER_ID: (state, userId) => {
      state.userId = userId
    },
    SET_DECIMAL_POINT: (state, decimalPoint) => {
      state.decimalPoint = decimalPoint
    },
  },

  actions: {
    // 登录
    Login({ commit }, userInfo) {
      return new Promise((resolve, reject) => {
        login(userInfo).then(response => {
          const result = response.result
          const time_limit = !result.time_limit?600:result.time_limit   //token期限，默认600秒
          Vue.ls.set(ACCESS_TOKEN, result.token, time_limit * 1000)
          commit('SET_TOKEN', result.token)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 获取用户信息
    GetInfo({ commit }) {
      return new Promise((resolve, reject) => {
        getInfo().then(response => {
          const result = response.result

          if (result.role && result.role.permissions.length > 0) {
            const role = result.role
            role.permissions = result.role.permissions
            role.permissions.map(per => {
              if (per.actionEntitySet != null && per.actionEntitySet.length > 0) {
                const action = per.actionEntitySet.map(action => { return action.action })
                per.actionList = action
              }
            })
            role.permissionList = role.permissions.map(permission => { return permission.permissionId })
            commit('SET_USER_ID', result.userId)
            commit('SET_MONEY_UNIT', result.monetaryyUnit)
            commit('SET_TIMEZONE', result.timezone)
            commit('SET_DEPT_ID', result.deptId)
            commit('SET_DATE_FORMAT', result.dateFormat)
            commit('SET_MOBILE', result.mobile)
            commit('SET_EMAIL', result.email)
            commit('SET_ROLES', result.role)
            commit('SET_INFO', result)
            commit('SET_NAME', { name: result.name, welcome: welcome() })
            if(process.env.NODE_ENV != 'production'){
              commit('SET_AVATAR', result.avatar)
            }
            else{
              commit('SET_AVATAR', '/inhe-platform-mdm/' + result.avatar)
            }
            localStorage.setItem('orgId',result.orgId)
          } else {
            reject('getInfo: roles must be a non-null array !')
          }

          resolve(response)
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 登出
    Logout({ commit, state }) {
      return new Promise((resolve) => {
        commit('SET_TOKEN', '')
        commit('SET_ROLES', [])
        Vue.ls.remove(ACCESS_TOKEN)
        logout(state.token).then(() => {
          // const url = 'http://' + result.result
          // window.location.href= url
          window.opener=null
          window.open('','_self')
          window.close()
          //resolve()
        }).catch(() => {
          window.opener=null
          window.open('','_self')
          window.close()
          resolve()
        })
      })
    },

  }
}

export default user