<template>
  <a-dropdown>
    <span class="ant-dropdown-link user-dropdown-menu">
      <span style="font-size: 16px;" :style="{ marginLeft: '-20px', marginTop: '-30px'}">&nbsp;&nbsp;{{ time }}</span>
    </span>
    <div slot="overlay" class="user-dropdown-menu-wrapper show-date">
      {{ date }}
    </div>
    
  </a-dropdown>
</template>
<script>
import { toFormatDate, getOffset } from '@/inhe/picker/utils'
import { axios } from '@/utils/request'
export default {
  name: 'SystemTime',
  data(){
    return {
      dateFormat: 'yyyy-MM-dd HH:mm:ss',
      timezone: 'Etc/GMT',
      date: '',
      time: '',
      currDate: new Date()
    }
  },
  props: {
    type: {
      type: String,
      default: '0'
    }
  },
  created(){
    this.loadTime()
    window.setInterval(()=>{
      this.loadTime()
    },10*60*1000)

    window.setInterval(()=>{
      this.incTime()
    }, 1000)
 
  },
  methods: {
    loadTime(){
      const that = this
      axios({
        url: '/auth/system-time',
        method: 'get',
      }).then(result=>{
        that.formatDate(result.result)
      })
    },
    incTime(){
      this.currDate.setSeconds(this.currDate.getSeconds() + 1)
      const dateTime = toFormatDate(this.currDate, this.dateFormat).split(' ')
      this.date = dateTime[0]
      this.time = dateTime[1]
    },
    formatDate(dateTime){
      if(!dateTime){
        return ''
      }
      this.dateFormat = this.$store._vm.userInfo.dateFormat

      this.currDate = new Date(dateTime)
      //计算时间差
      const offset = getOffset(this.$store._vm.userInfo.timezone)
      this.currDate.setMinutes(this.currDate.getMinutes() + offset)
      dateTime = toFormatDate(this.currDate, this.dateFormat).split(' ')
      this.date = dateTime[0]
      this.time = dateTime[1]
    }
  }
}
</script>
<style>
.show-date{
  border: 1px solid #d9d9d9; 
  border-radius: 4px;
  background: #ffffff;
  font-size: 25px;
  padding: 2px 8px;
}
</style>