import Vue from 'vue'
import VueI18n from 'vue-i18n'
import { getLang, getLangList } from '@/api/manage'

Vue.use(VueI18n)

const messages = {}
let langDetail = {}
const antLangs = {}
const i18n = new VueI18n({
  messages
})

let langList = null
function loadLangs(callback){
  if(langList == null){
    getLangList().then(response => {
      langList = response.result
      callback?callback():''
    })
  }
  else{
    callback?callback():''
  }
}

function changeLang(that,id,callback){
  loadLangs(function(){
    if(!Object.prototype.hasOwnProperty.call(langList,id)){
      id = getLocalLang()
    }
    getLang(id).then(response => {
      const pack = response.result
      langDetail = pack.detail
      i18n.mergeLocaleMessage(pack.iso, pack.detail)
      if(!Object.prototype.hasOwnProperty.call(messages,id)){
        import(`./langs/${id}`).then(msgs => {
          i18n.setLocaleMessage(id, msgs.default)
          i18n.locale = id
          localStorage.setItem('locale', id)
        }) 
        import(`ant-design-vue/lib/locale-provider/${id}`).then(msgs => {
          antLangs[id] = msgs.default
          that.$store.dispatch('setLangId', id)
        }) 
      }
      else{
        i18n.locale = id
        localStorage.setItem('locale', id)
        that.$store.dispatch('setLangId', id) //防止异步问题导致切换延迟
      }
      callback?callback():''
    })
  })
}

function getLocalLang(){
  let language = null
  if (navigator.appName == 'Netscape'){
    language = navigator.language
  }
  else{
    language = navigator.browserLanguage
  }
  if (language.indexOf('zh') > -1){return 'zh_CN'}
  else if (language.indexOf('en') > -1) {return 'en_US'}
  else if (language.indexOf('fr') > -1) {return 'fr_FR'}
  else if (language.indexOf('nl') > -1) {return 'nl-NL'}
  else if (language.indexOf('de') > -1) {return 'de-DE'}
  else if (language.indexOf('ja') > -1) {return 'ja-JP'}
  else if (language.indexOf('it') > -1) {return 'it-IT'}
  else if (language.indexOf('pt') > -1) {return 'pt-PT'}
  else if (language.indexOf('es') > -1) {return 'es'}
  else if (language.indexOf('sv') > -1) {return 'sv-fi'}
  else    return 'zh_CN'
}

function getCurrId(){
  return localStorage.getItem('locale')
}

function getAntdLang(id){
  return antLangs[id]
}

function getLangs(callback){
  loadLangs(function(){
    const result = []
    for (const key in langList) {
      result.push({id: key, title: langList[key].title, img: langList[key].img})
    }
    callback(result) 
  })
}

function $lang(key1, key2){
  if(!langDetail){
    return undefined
  }
  const v1 = langDetail[key1]
  if(!v1){
    return undefined
  }
  const v2 = v1[key2]
  if(!v2){
    return undefined
  }
  return v2
}

export { getCurrId ,getLangs ,getAntdLang ,changeLang, $lang, i18n } 