<template>
  <a-locale-provider :locale="langId">
    <div id="app">
      <router-view/>
    </div>
  </a-locale-provider>
</template>
<script>
  import enquireScreen from '@/utils/device'
  import { version } from 'ant-design-vue'
  import { mapState } from 'vuex'
  import { getAntdLang, changeLang } from '@/i18n/i18n'
  export default {
    data () {
      return {
        version,
        andtLang: null
      }
    },
    computed: {
      ...mapState({
        langId: (state) => getAntdLang(state.app.langId)
      })
    },
    created () {
      const that = this
      that.$message.config({
        top: `300px`,
        duration: 1,
        maxCount: 1,
      })
      console.log('use Ant-Design Of Vue:', version)
      changeLang(that, localStorage.getItem('locale'))
      enquireScreen(deviceType => {
        // tablet
        if (deviceType === 0) {
          that.$store.commit('TOGGLE_DEVICE', 'mobile')
          that.$store.dispatch('setSidebar', false)
        }
        // mobile
        else if (deviceType === 1) {
          that.$store.commit('TOGGLE_DEVICE', 'mobile')
          that.$store.dispatch('setSidebar', false)
        }
        else {
          that.$store.commit('TOGGLE_DEVICE', 'desktop')
          that.$store.dispatch('setSidebar', true)
        }

      })
    }
  }
</script>
