<template>
  <a-popover
    trigger="click"
    placement="bottomRight"
    :autoAdjustOverflow="true"
    :arrowPointAtCenter="true"
    overlayClassName="header-notice-wrapper"
    v-model="visible"
    :overlayStyle="{ width: '150px', top: '50px' }">
    <template slot="content">
      <a-list
        itemLayout="horizontal"
        :dataSource="data"
      >
        <a-list-item slot="renderItem" slot-scope="item">
          <a-list-item-meta>
            <a-avatar slot="avatar" size="small" shape="square" v-text="item.title.substr(0,1)" :style="{backgroundColor: '#f56a00'}" />
            <a slot="title" v-text="item.title" @click="onChange(item)"></a>
          </a-list-item-meta>
        </a-list-item>
      </a-list>
    </template>
    <span class="header-notice">
      <a-icon style="font-size: 20px; padding:0 2px;" type="global" />
    </span>
  </a-popover>
</template>
<script>
  import { getLangs, changeLang } from '@/i18n/i18n'
  export default {
    name: 'LangChange',
    data () {
      return {
        visible: false,
        data: null
      }
    },
    mounted() {
      const that = this
      getLangs(function(result){
        that.data = result
      })
    },
    methods: {
      onChange(item){
        changeLang(this,item.id)
      }
    }
  }
</script>

<style lang="css">
  .header-notice-wrapper {
    top: 50px !important;
  }
</style>
<style lang="scss" scoped>
  .header-notice{
    display: inline-block;
    transition: all 0.3s;
    span {
      vertical-align: initial;
    }
  }
</style>