<template>
  <span>
    <a-date-picker
      :showTime="true"
      :format="format"
      :value="value"
      :size="size"
      :disabled="disabled"
      @change="onChange"
      style="width: 100%"
    />
  </span>
</template>
<script>
import moment from 'moment'
import { toFormatDate, toUTC, getOffset } from '@/inhe/picker/utils'
import 'moment/locale/zh-cn'
export default {
  name: 'PickTime',
  data(){
    return {
      format: 'YYYY-MM-DD HH:mm:ss',
      offset: 0,
      value: ''
    }
  },
  props: {
    defaultValue: {
      type: String,
      default: ''
    },
    size: {
      type: String,
      default: 'default'
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    defaultValue(value) {
      this.value = this.formatDate(value)
    }
  },
  created(){
    this.format = this.$store._vm.userInfo.dateFormat.replace(/y/g, 'Y').replace(/d/g, 'D')
    this.offset = getOffset(this.$store._vm.userInfo.timezone)
    this.value = this.formatDate(this.defaultValue)
  },
  methods: {
    formatDate(value){
      if(!value){
        return null
      }
      const date = new Date(value)
      date.setMinutes(date.getMinutes() + this.offset)
      return moment(toFormatDate(date, this.format.replace(/Y/g, 'y').replace(/D/g, 'd')), this.format)
    },
    onChange(value) {
      this.value = value
      if(value){
        const date = new Date(value)
        date.setMinutes(date.getMinutes() - this.offset)
        this.$emit('change', toUTC(date))
      }
      else{
        this.$emit('change')
      }
    }
  }
}
</script>