<template>
  <span v-if="visible">
    <a-upload 
      style="display: inline-block;"
      :headers="headers"
      :action="action"
      :data="data"
      :showUploadList="false"
      :multiple="multiple"
      :accept="accept"
      @change="onChange"
      name="fileName"
      :beforeUpload="beforeUpload"
      v-if="type=='import'"
    >
      <a-button type="primary" icon="upload" ghost> {{ !text?$t('button._007'):text }}</a-button>
    </a-upload>
    <a-button type="primary" icon="upload" ghost @click="click" v-if="type=='bt'"> {{ !text?$t('button._007'):text }} </a-button>
    <span @click="click" v-if="type=='b'">{{ !text?$t('button._007'):text }}</span>
  </span>
</template>
<script>
import notification from 'ant-design-vue/es/notification'
export default {
  name: 'BtnImport',
  data() {
    return {
      id: '007',
      visible: false,
      headers:{}
    }
  },
  props: {
    type: {
      type: String,
      default: 'import'
    },
    action: {
      type: String,
      default: ''
    },
    multiple: {
      type: Boolean,
      default: false
    },
    accept: {
      type: String,
      default: ''
    },
    beforeUpload: {
      type: Function,
      default: null
    },
    text: {
      type: String,
      default: null
    },
    data:{
      type:Object,
      default:null
    }
  },
  created() {
    const that = this
    that.$store._vm.roles.permissions.forEach(element => {
      if(element['permissionId']==that.$route.name && element['actionList'].indexOf(that.id)>=0){
        that.visible = true
      }
    })
    that.headers['Access-Token'] = that.$store._vm.token
  },
  methods: {
    //仅仅作为导出按钮，不进行导出
    click(){
        const that = this
        that.$emit('click')
    },
    onChange(object){
      if(object.file.status == 'uploading'){
        this.$emit('uploading')
      }
      else
      if(object.file.status == 'done'){
        if(object.file.response.code == 0){
          this.$emit('done',object.file.response)
          notification.success({ 
            message: 'Upload file', 
            description: this.$t('common.msg2')
          })
        }
        else{
          notification.error({ 
            message: object.file.response.code, 
            description: object.file.response.message?object.file.response.message:'unknow error'
          })
        }
      }
      else
      if(object.file.status == 'error'){
        this.$emit('error')
        notification.error({ 
          message: -100000, 
          description: this.$t('common.msg1')
        })
      }
    }
  }
}
</script>