<template>
  <span v-if="visible">
    <a-button type="primary" icon="download" @click="click" v-if="type=='bt'" ghost>{{ !text?$t('button._008'):text }}</a-button>
    <a @click="click" v-if="type=='a'">{{ !text?$t('button._008'):text }}</a>
    <span @click="click" v-if="type=='b'">{{ !text?$t('button._008'):text }}</span>
  </span>
</template>
<script>
export default {
  name: 'BtnExport',
  data() {
    return {
      id: '008',
      visible: false
    }
  },
  props: {
    type: {
      type: String,
      default: 'bt'
    },
    confirm: {
      type: Boolean,
      default: true
    },
    text: {
      type: String,
      default: null
    }
  },
  created() {
    const that = this
    that.$store._vm.roles.permissions.forEach(element => {
      if(element['permissionId']==that.$route.name && element['actionList'].indexOf(that.id)>=0){
        that.visible = true
      }
    })
  },
  methods: {
    click(){
      const that = this
      if(that.confirm){
        that.$confirm({
          title: that.$t('button._008_msg1'),
          content: that.$t('button._008_msg2'),
          okType: 'danger',
          onOk() {
            that.$emit('click')
          }
        })
      }
      else{
        that.$emit('click')
      }
    }
  }
}
</script>