import { axios } from '@/utils/request'

const api = {
  user: '/user',
  role: '/role',
  service: '/service',
  permission: '/permission',
  permissionNoPager: '/permission/no-pager',
}

export default api

export function getUserList(parameter) {
  return axios({
    url: api.user,
    method: 'get',
    params: parameter
  })
}

export function getRoleList(parameter) {
  return axios({
    url: api.role,
    method: 'get',
    params: parameter
  })
}

export function getServiceList(parameter) {
  return axios({
    url: api.service,
    method: 'get',
    params: parameter
  })
}

export function getPermissions(parameter) {
  return axios({
    url: api.permissionNoPager,
    method: 'get',
    params: parameter
  })
}

// id == 0 add     post
// id != 0 update  put
export function saveService(parameter) {
  return axios({
    url: api.service,
    method: parameter.id == 0 ? 'post' : 'put',
    data: parameter
  })
}

export function getLangList() {
  return axios({
    url: '/auth/lang-list',
    method: 'get',
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    }
  })
}

export function getLang(isoCode) {
  return axios({
    url: '/auth/lang-info',
    method: 'get',
    params: {isoCode: isoCode},
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    }
  })
}

export function getWebClientParams(orgId) {
  return axios({
    url: '/auth/web-client-params',
    method: 'get',
    params: {orgId: orgId?orgId: ''},
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    }
  })
}