<template>
  <span v-if="spanType">{{ value }}</span>
  <p v-else>{{ value }}</p>
</template>
<script>
import { toFormatDate, getOffset } from '@/inhe/picker/utils'
export default {
  name: 'PickLabel',
  data(){
    return {
      dateFormat: 'yyyy-MM-dd HH:mm:ss',
      timezone: 'Etc/GMT',
      value: ''
    }
  },
  props: {
    defaultValue: {
      type: String,
      default: ''
    },
    showDate: {
      type: Boolean,
      default: true
    },
    showTime: {
      type: Boolean,
      default: true
    },
    showSecond: {
      type: Boolean,
      default: true
    },
    spanType: {
      type: Boolean,
      default: true
    },
    spanMonth: {
      type: Boolean,
      default: true
    },
    
  },
  watch: {
    defaultValue(value) {
      this.value = this.formatDate(value)
    }
  },
  created(){
    this.value = this.formatDate(this.defaultValue)
  },
  methods: {
    formatDate(dateTime){
      if(!dateTime){
        return ''
      }
      this.dateFormat = this.$store._vm.userInfo.dateFormat
      if(!this.showTime){
        //得出 dd/MM/yyyy
        this.dateFormat = this.dateFormat.replace(/[H,m,s,:,\s]/g,'')
      }
      if(!this.showDate){
        //得出 MM/yyyy
        this.dateFormat = this.dateFormat.replace(/dd\//g,'').replace(/[H,m,s,:,\s]/g,'')
        this.dateFormat = this.dateFormat.replace(/\/dd/g,'').replace(/[H,m,s,:,\s]/g,'')
        this.dateFormat = this.dateFormat.replace(/-dd/g,'').replace(/[H,m,s,:,\s]/g,'')
        this.dateFormat = this.dateFormat.replace(/dd-/g,'').replace(/[H,m,s,:,\s]/g,'')
      }
      if(!this.showSecond){
        this.dateFormat = this.dateFormat.replace(/:ss/g,'')
      }
      var date = new Date(dateTime)
      //计算时间差
      const offset = getOffset(this.$store._vm.userInfo.timezone)
     
      date.setMinutes(date.getMinutes() + offset)
      return toFormatDate(date, this.dateFormat)
    }
  }
}
</script>